</template>
<script>
import productHover from "@/components/coffee/ProductHover.vue";
import firstSelection from "@/views/coffee/Home/components/first-selection.vue";
import secondSelection from "@/views/coffee/Home/components/second-selection.vue";
import threeSelection from "@/views/coffee/Home/components/three-selection.vue";

export default {
  name: "home",
  components: {productHover, firstSelection, secondSelection, threeSelection},
  data() {
    return {
      fourList: {
        top: [
          {
            img: require("@/assets/images/门店简介配图/门店简介/1.png"),
            title: "Sand noodle 1 shop"
          },
          {
            img: require("@/assets/images/门店简介配图/门店简介/2.png"),
            title: "Shamian flagship store"
          }
        ],
        bottom: [
          {
            img: require("@/assets/images/门店简介配图/门店简介/3.jpg"),
            title: "Sihai City store"
          }
        ],
      },
      show_second: false,
      show_three: false,
      show_four: false,
      show_five: false
    }
  },
  destroyed() {
    window.removeEventListener('scroll', this.handleScrollbox)
  },
  methods: {
    handleScrollbox() {
      this.currentScroll = window.pageYOffset //表示当前滚动的位置
      if (this.currentScroll >= 350) {
        this.show_second = true;
      }
      if (this.currentScroll >= 1100) {
        this.show_three = true;
      }
      if (this.currentScroll >= 2050) {
        this.show_four = true;
      }
      if (this.currentScroll >= 2800) {
        this.show_five = true;
      }
    },
  },
  mounted() {
    window.addEventListener('scroll', this.handleScrollbox)
    const scroll = this.$route.query.scroll;
    if (scroll) {
      document.getElementById(scroll).scrollIntoView({
        behavior: "smooth",// 过渡动画效果 详细看文档
        block: "start" // 滚动到的位置 可选start center end
      });
    }
  }
}
</script>


<style scoped lang="scss">
.bg-grey {
  background: url("@/assets/bg/texture-overlay--grey.png");
}

