<script>
export default {
  name: "three-selection",
  props: {
    showList: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      threeList: [
        // {
        //   img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/1.jpg"),
        //   series: "Smoothie series",
        //   title: "Chocolate Coconut Ice cream"
        // },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/2.jpg"),
          en: {
            series: "Creative coffee",
            title: "Sparkling orange ice extract"
          },
          sc: {
            series: "创意咖啡",
            title: "气泡橙香冰萃"
          },
          tc: {
            series: "創意咖啡",
            title: "氣泡橙香冰萃"
          }
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/3.jpg"),
          en: {
            series: "Boutique pour-over coffee series",
            title: "Pour-over coffee"
          },
          sc: {
            series: "",
            title: "精品手冲系列"
          },
          tc: {
            series: "",
            title: "精品手沖系列"
          }
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/4.jpg"),
          en: {
            series: " ",
            title: "Cold brew coffee"
          },
          sc: {
            series: " ",
            title: "冷萃咖啡"
          },
          tc: {
            series: " ",
            title: "冷萃咖啡"
          }
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/5.jpg"),
          en: {
            series: "Creative coffee",
            title: "Sparkling orange ice extract"
          },
          sc: {
            series: "创意咖啡",
            title: "气泡橙香冰萃"
          },
          tc: {
            series: "創意咖啡",
            title: "氣泡橙香冰萃"
          }
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/6.jpg"),
          en: {
            series: "Smoothie series",
            title: "Coffee coconut ice smoothie"
          },
          sc: {
            series: "冰沙系列",
            title: "咖啡椰子冰雪乐"
          },
          tc: {
            series: "冰沙系列",
            title: "咖啡椰子冰雪樂"
          }
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/7.jpg"),
          en: {
            series: " ",
            title: "Kopi Luwak, three ways to enjoy with one bean"
          },
          sc: {
            series: " ",
            title: "猫屎咖啡一豆三喝"
          },
          tc: {
            series: " ",
            title: "貓屎咖啡一豆三喝"
          }
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/8.jpg"),
          en: {
            series: "Creative coffee",
            title: "Sparkling orange-flavored iced coffee extract"
          },
          sc: {
            series: "创意咖啡",
            title: "气泡橙香冰萃"
          },
          tc: {
            series: "創意咖啡",
            title: "氣泡橙香冰萃"
          }
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/9.jpg"),
          en: {
            series: " ",
            title: "Cold brew coffee"
          },
          sc: {
            series: " ",
            title: "冷萃咖啡"
          },
          tc: {
            series: " ",
            title: "冷萃咖啡"
          }
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/10.jpg"),
          en: {
            series: "Boutique pour-over coffee series",
            title: "Pour-over coffee"
          },
          sc: {
            series: " ",
            title: "精品手冲系列"
          },
          tc: {
            series: " ",
            title: "精品手沖系列"
          }
        },
        {
          img: require("@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/11.jpg"),
          en: {
            series: " ",
            title: "Kopi Luwak, three ways to enjoy with one bean"
          },
          sc: {
            series: " ",
            title: "猫屎咖啡一豆三喝"
          },
          tc: {
            series: " ",
            title: "貓屎咖啡一豆三喝"
          }
        }
      ],
      colWidth: 0,
      containerWidth: 0,
      showCol: [],
      curTranslateX: 0,
      curLineLeft: 0
    }
  },
  computed: {
    disablePrev() {
      return this.curTranslateX === 0
    },
    disableNext() {
      return this.curLineLeft.toFixed(2) === (100 - 100 / (this.threeList.length - this.showCol.length + 1)).toFixed(2)
    }
  },
  methods: {
    move(num) {
      if (num < 0) {
        if (this.disableNext) return;
        this.curLineLeft += 100 / (this.threeList.length - this.showCol.length + 1);
        this.$refs.lightLine.style.left = `${this.curLineLeft}%`
      } else {
        if (this.disablePrev) return;
        this.curLineLeft -= 100 / (this.threeList.length - this.showCol.length + 1);
        this.$refs.lightLine.style.left = `${this.curLineLeft}%`
      }
      this.curTranslateX += num
      this.$refs.colBoxRef.$el.style.transform = `translateX(${this.curTranslateX}px)`;
    }
  },
  watch: {
    // 监听showList的变化，当showList为true时，执行下面的代码
    showList(val) {
      if (val) {
        this.$nextTick(() => {
          const colRef = this.$refs.colRef
          const containerRef = this.$refs.containerRef
          this.containerWidth = containerRef.getBoundingClientRect().right;
          // 60是左右30px的外币那句
          this.colWidth = colRef[0].$el.offsetWidth + 60;
          colRef.forEach(item => {
            const right = item.$el.getBoundingClientRect().right;
            if (right < this.containerWidth) {
              this.showCol.push(item)
            }
          })
          // 未显示的几个加一个
          this.$refs.lightLine.style.width = `${100 / (this.threeList.length - this.showCol.length + 1)}%`;
          this.$refs.lightLine.style.left = `${this.curLineLeft}%`
        })
      }
    }
  }
}
</script>

<template>
  <div class="sm:min-h-screen box-content h-auto bg-grey 2xl:h-screen xl:pb-10 2xl:pb-20" id="threePage">
    <div
        class="container mx-auto w-full relative border-b border-gray-400 pb-10 sm:px-20 lg:px-10 xl:px-20  px-3"
        v-show="showList">
      <div class="animate__animated animate__fadeInUp">
        <p class="text-coffee text-md mt-10">{{ $t('coffee.home.three.title') }}</p>
        <p class="text-3xl my-5 font-bold">{{ $t('coffee.home.three.content1') }} <br/>
          {{ $t('coffee.home.three.content2') }}</p>
        <p class="text-coffee">
          {{ $t('coffee.home.three.desc') }}
        </p>
      </div>
      <div
          class="w-screen h-72 sm:h-auto overflow-x-auto hidden-scrollbar sm:pr-60 animate__animated animate__slideInRight"
          ref="containerRef">
        <el-row :gutter="20" ref="colBoxRef"
                class="flex w-full mt-10 2xl:py-5 lg:py-16 transition-all">
          <el-col :sm="5" :xs="8" v-for="(item,index) in threeList" :key="index" ref="colRef"
                  class="2xl:h-102 h-40 lg:h-60 flex-shrink-0 mx-3 sm:mx-[30px]">
            <img class="w-full h-5/6 2xl:h-3/4 transform mb-6"
                 :class="[index %2 === 0 ? '-rotate-2 translate-y-3' : 'rotate-2 -translate-y-3']"
                 :src="item.img" alt="">
            <p class="sm:text-sm text-xs">{{ item[$i18n.locale].series }}</p>
            <span class="font-bold sm:text-lg text-sm">{{ item[$i18n.locale].title }}</span>
          </el-col>
        </el-row>
      </div>
      <div class="flex items-center mt-10 2xl:mt-0">
        <div @click="move(colWidth)" :class="{'grayscale filter': disablePrev}"
             class="rounded-full border-coffeeLight border-2 z-50 cursor-pointer mx-3">
          <img class="w-10 h-10" src="@/assets/icon/toLeft-coffee.png" alt="">
        </div>
        <div class="flex-grow relative h-1">
          <!--          灰色-->
          <div class="w-full rounded-xl h-full bg-gray-500"></div>
          <!--          高亮-->
          <div class="bg-coffeeLight absolute -top-1/4 rounded-xl h-1.5 transition-all" ref="lightLine"></div>
        </div>
        <div @click="move(-colWidth)" :class="{'grayscale filter': disableNext}"
             class="rounded-full border-coffeeLight border-2 z-50 cursor-pointer mx-3">
          <img class="w-10 h-10" src="@/assets/icon/toRight-coffee.png" alt="">
        </div>
      </div>
    </div>
  </div>
</template>

