<!--suppress ALL -->
<script>
export default {
  name: "first-selection",
  data() {
    return {
      imgList: [
        {
          img: require('@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/11.jpg'),
          id: 2
        },
        {
          img: require('@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/3.jpg'),
          id: 1
        },
        {
          img: require('@/assets/images/饮品&咖啡商品配图/现制咖啡饮品/7.jpg'),
          id: 0
        }
      ],
      curIndex: 0
    }
  },
  methods: {
    toLeft() {
      this.computedIndex(-1);
      this.toIndex(this.curIndex)
    },
    toRight() {
      this.computedIndex(1)
      this.toIndex(this.curIndex)
    },
    computedIndex(num) {
      this.curIndex += num;
      if (this.curIndex < 0) {
        this.curIndex = 2;
      } else if (this.curIndex > 2) {
        this.curIndex = 0;
      }
    },
    toIndex(index) {
      this.curIndex = index;
      const swiper = this.imgList.find(item => item.id === index);
      this.imgList = this.imgList.filter(item => {
        return item.id !== index
      })
      this.imgList.push(swiper)
    }
  }
}
</script>

<template>
  <div class="min-h-screen bg-grey h-screen">
    <el-backtop></el-backtop>
    <div
        class="container mx-auto flex flex-col sm:flex-row items-center sm:justify-between justify-center h-full border-b pb-10 sm:pb-0">
      <div class="flex flex-col items-center sm:w-1/2 w-full order-2 sm:order-1 mt-10 sm:mt-0">
        <p class="text-5xl animate__animated animate__fadeInUp">Kafelaku</p>
        <p class="text-2xl my-5 animate__animated animate__fadeInUp animate__delay-1s">Coffee</p>
        <p class="w-4/5 text-center sm:text-left animate__animated animate__fadeInUp animate__delay-2s mb-5">
          {{ $t('coffee.home.first.content') }}
        </p>
        <el-button type="primary" round class="animate__animated animate__fadeInUp animate__delay-2s">
          {{ $t('coffee.home.first.button') }}
        </el-button>
        <div class="flex mt-5 animate__animated animate__fadeInUp animate__delay-2s">
          <div v-for="item in 3" :key="item" class="swiper-point-grey cursor-pointer"
               :class="{'swiper-point-active': item-1===curIndex}" @click="toIndex(item-1)"></div>
        </div>
      </div>
      <div
          class="sm:w-1/2 w-5/6 h-1/2 flex justify-center items-end animate__rotateInDownRight animate__animated order-1 sm:order-2">
        <div class="relative img-swiper z-10 w-96 sm:h-full h-3/4 flex items-center justify-center">
          <img class="transform animate__fadeIn animate__animated h-1/2 w-full"
               :class="{'-rotate-1': index===2, '-rotate-3': index===1,'-rotate-4': index===0}"
               v-for="(item,index) in imgList"
               :key="item.id" :src="item.img" alt="">
          <div @click="toLeft"
               class="absolute top-1/2 left-2 transform -translate-y-1/2 border rounded-full border-white z-50 cursor-pointer">
            <img class="w-10 h-10" src="@/assets/icon/toLeft.png" alt="">
          </div>
          <div @click="toRight"
               class="absolute top-1/2 right-2 transform -translate-y-1/2  border rounded-full border-white z-50 cursor-pointer">
            <img class="w-10 h-10" src="@/assets/icon/toRight.png" alt="">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped lang="scss">

.img-swiper {
  & > img {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    flex-shrink: 1;
  }
}

.swiper-point-grey {
  width: 10px;
  height: 10px;
  background-color: #ccc;
  margin: 0 5px;
  border-radius: 50%;
  transition: all .3s;
}

.swiper-point-active {
  background-color: white;
  border: 2px solid #BC9B5D;
  transform: scale(1.2);
}
</style>